@use 'base/variables' as v;

.iconos-nosotros{
    display: flex;
    flex-direction: column;
    justify-content: left;
  
    padding: 2.5rem 0;
    margin: 0;
}
.reservar {
    height: 80%;
}
.icono{
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: left;
    &-sep{
        margin-left: 3rem;
    }
}
.icono-texto{
    margin: 0;
    display: flex;
    color: v.$azul;
    justify-content: space-evenly;
    &--derecha{
        justify-content: right;
        padding-right: 2rem;
    }
    
}
