@use 'base/variables' as v;

.alerta{
    padding: calc(v.$separacion / 4);
    color: v.$blanco;
    width: 100%;
    display: block;
    margin-bottom: 1.5rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    border-radius: .5rem;
    &.error{ // El & es para Incicar que cuando este presente
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
}