@use 'base/variables' as v;
@use 'base/mixins' as m;

.menu-redes{
    @include m.menu-social(v.$verde, 2.5rem);
    display: flex;
    flex-direction: row;
    padding-left: 5rem;
    gap: 2rem;
    justify-content: left;
    @include m.tablet{
        justify-content: center;
        margin: 0;
        gap: 1rem;
    }
}