@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
        @include m.grid(2,1fr);
        min-height: 100vh;
    }
    .imagen{
        height: 20rem;
        background-image: url(../img/1.jpg);
        background-size: cover;
        opacity: .5;
        background-position: center center;
        @include m.tablet{
            height: auto;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet{
            overflow: scroll;
        }
    }
    
}

.barra{
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
    };

        margin: v.$separacion 0;
    .boton{
        @include m.boton (v.$negro, v.$blanco);
        margin: 0;
    }
    
}
.barra-servicios{
    margin-bottom: v.$separacion;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }
    .boton{
        margin: 0 0 1rem 1rem;
    }
}

.navegacion {

    //visibility: hidden;
    opacity: 1;
    height: 9rem;
    margin: 1rem 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 

    @include m.tablet {
        height: 0;
        padding: 0;
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    
    a {
        color: v.$azul;
        text-decoration: none;
        font-size: 1.5rem;
        display: block;
        text-align: center;
        padding: .3rem 0;

        @include m.tablet {
            margin-right: 2rem;
            font-size: 1.5rem;
            text-align: left;
            &:last-of-type {
                margin-right: 0;
            }
        }

        &:hover {
            color: v.$verde;
        }
    }
}

.logo{
    background-image: url(../img/logo.jpeg);
    background-size: cover;
    background-position: center center;    
}

.mapa{
    height: 25rem;
    width: 99%;
    margin-top: 3rem;
    &__heading{
        margin: 0;
        color: v.$azul;
        font-size: medium;
        text-align: center;
    }
    &__texto{
        margin: 0;
        color: darken(v.$gris,30%);
        font-size: medium;
    }
    &__email, &__telefono{
        margin: 0;
        color: v.$gris;
        font-size: small;
    }
}
.footer {
    width: 99%;
    padding: calc(2.5 / 1) 0;
    //background-color: v.$azul;
    margin-bottom: 0;

    .contenedor-footer {
        padding-top: calc(2.5 / 1) 0;
    }
    .copyright {
        color: v.$blanco;
        text-align: center;
        font-size: small;
    }
}
.footer-regreso{
    color: v.$azul;
    text-decoration: none;
    font-size: 2rem;
    display: block;
    text-align: right;
    padding-right: 2rem;
    padding-top: 2rem;
    &:hover {
        color: v.$verde;
    }
}

.centrar{
    display: flex;
    justify-content: center;
}
.entrada-blog {
    margin-bottom: 2rem;
    
    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 1rem;
    }

}
.img{
    border-radius: 1rem;
}
.texto-entrada {
    a {
        color: v.$azul;
        font-size: v.$black;
        margin-top: 1rem;
        display: block;
        padding: .5rem 0;
        @include m.tablet {
            margin-top: 0;
        }
    }
    span{
        font-weight: bold;
    }
    h4 {
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: v.$verde;
            margin-top: 1rem;
        }
    }

}
