@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton{
    @include m.boton( v.$azul,v.$blanco);
}

.boton-eliminar{
    @include m.boton(v.$rojo,v.$blanco); 
}
.boton-confirmar{
    @include m.boton(v.$verde,v.$blanco); 
}

.botones{
    display: flex;
    justify-content: space-between;
}