@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 5rem 0;
    border-radius: .5rem;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 15% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin menu-social($color, $size){
    &__enlace{

        &::before{
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: 'Font Awesome 6 Brands';  
        }
        &[href*="facebook.com"]::before {
            content: "\f39e";
        }
        &[href*="twitter.com"]::before {
            content: "\f099";
        }
        &[href*="youtube.com"]::before {
            content: "\f167";
        }
        &[href*="instagram.com"]::before {
            content: "\e055";
        }
        &[href*="https://tiktok.com"]::before {
            content: "\e07b";
        }
        &[href*="github.com"]::before {
            content: "\f09b";
        }
    }
    &__ocultar{ display: none;}
}

@mixin enlace{
    all: unset;
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight:bold;
    &:hover{
        cursor: pointer;
        color: v.$azul;
    }
}